<script>
import axios from "axios";

export default {
  name: "FQ12-06-00",
  data: () => {
    return {
      formcode: "FQ12-06-00",
      nationalno: "",
      form: {
        cfname: "",
        clname: "",
        father: "",
        edudepname: "",
      },
    };
  },
  methods: {
    async submitForm() {
      await axios
        .post("http://localhost:3000/forms/", {
          formcode: this.formcode,
          nationalno: this.nationalno,
          content: this.form,
        })
        .then((res) => {
          if (res.status == 200) {
            alert("فرم ایجاد شد");
            window.location.reload();
          } else {
            alert("خطا در پردازش درخواست");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

